<template>
    <view>
        <input type="text" :value="value" @input="onInput"  v-bind="$attrs" placeholder="请输入" />
    </view>
</template>

<script>
    export default {
        name:"sj-input",
        inheritAttrs: false,
        data() {
            return {
                
            };
        },
        
        inject: ['formItem'],
        
        props: {
            value: String
        },
        
        methods: {
            onInput(e) {
                this.$emit('on-input', e.target.value)
                this.formItem.$emit('validate')
            }
        }
    }
</script>

